import FormTemplate from "../FormTemplate";
import breads from '../../services/Grains&Bakery/breads';

const Breads1 = () => {
        const handleSubmit = ({ productName, productType, productAmount, calculateValue }) => {
                console.log(`Product name: ${productName}, product type: ${productType}, product amount: ${productAmount},${calculateValue(productName, productAmount, productType)}`);
        };


        return (
                <div>

                        <FormTemplate
                                title="לחמים"
                                productName1="סוג הלחם"
                                products={breads}
                                productName={breads[0]['details']['productName']}
                                onSubmit={handleSubmit}
                        // result={calculateValue(productName, productAmount, productType)}
                        />
                </div>
        );

};
export default Breads1;

